import React, { useState } from 'react';
import PropTypes from 'prop-types';
import VisibilitySensor from 'react-visibility-sensor';
import { useCountUp } from 'react-countup';
import * as Styles from './IconWIthTitle.styles';

const IconWithTitle = ({ num, text, icon, ...props }) => {
  const [isOnceVisible, setIsOnceVisible] = useState(false); // [значение, функция]
  const { countUp, start } = useCountUp({
    // хук от компонента react-countup
    end: num,
    suffix: '+',
    startOnMount: false,
    ...props,
  });
  return (
    <VisibilitySensor
      onChange={(isVisible) => {
        // во избежание loop'a
        if (isVisible && !isOnceVisible) {
          setIsOnceVisible(true);
          start();
        }
      }}
    >
      <Styles.IconWithTitle>
        <Styles.IconWrapper>{icon}</Styles.IconWrapper>
        <Styles.TitleNumWrapper>{countUp}</Styles.TitleNumWrapper>
        <Styles.Text>{text}</Styles.Text>
      </Styles.IconWithTitle>
    </VisibilitySensor>
  );
};

IconWithTitle.propTypes = {
  num: PropTypes.number,
  text: PropTypes.string,
  // eslint-disable-next-line react/forbid-prop-types
  icon: PropTypes.any,
};

IconWithTitle.defaultProps = {
  num: 0,
  text: null,
  icon: null,
};

export default IconWithTitle;
