import React, { useEffect, useState } from 'react';
import { Container, Nav } from 'react-bootstrap';
// import {} from 'react-bootstrap/Navbar';

import * as Styles from './Navbar.styles';

export default () => {
  const [visibleNavbar, setVisibleNavbar] = useState(false);

  function onListen() {
    if (window.scrollY >= 80 && !visibleNavbar) {
      setVisibleNavbar(true);
    } else {
      setVisibleNavbar(false);
    }
  }

  useEffect(() => {
    // componentDidMount аналог на хуках
    window.addEventListener('scroll', onListen);

    // componentWillUnmount
    return () => {
      window.removeEventListener('scroll', onListen);
    };
  }, []);
  // if (!visibleNavbar) return false;
  return (
    <Styles.NavBar isVisible={visibleNavbar} variant="dark" fixed="top">
      <Container>
        <Styles.NavLogoWrapper>
          <Styles.NavLogo
            href="#home"
            src="https://designseer.com/html/bent-demo/images/logo.png"
            height="45"
            paddingTop="20"
            className="d-inline-block align-top"
            alt="Bent Logo"
          ></Styles.NavLogo>
        </Styles.NavLogoWrapper>
        <Nav className="ml-auto">
          <Styles.NavLink href="#HOME">HOME</Styles.NavLink>
          <Styles.NavLink href="#ABOUT">ABOUT</Styles.NavLink>
          <Styles.NavLink href="#FEATURES">FEATURES</Styles.NavLink>
          <Styles.NavLink href="#SCREENS">SCREENS</Styles.NavLink>
          <Styles.NavLink href="#DOWNLOAD">DOWNLOAD</Styles.NavLink>
          <Styles.NavLink href="#CONTACT">CONTACT</Styles.NavLink>
        </Nav>
      </Container>
    </Styles.NavBar>
  );
};
